import { type Locale } from '@/lib/i18n';
import Hero from '@/components/sections/Hero';
import Concept from '@/components/sections/Concept';
import Games from '@/components/sections/Games';
import Pricing from '@/components/sections/Pricing';
import Contact from '@/components/sections/Contact';
import Footer from '@/components/Footer';
import WhatsAppButton from '@/components/WhatsAppButton';
import { getContent } from '@/lib/content';

export default async function HomePage({ params }: { params: Promise<{ locale: string }> }) {
  const { locale } = await params;
  const validLocale = (locale === 'he' || locale === 'en' ? locale : 'en') as Locale;
  const content = getContent(validLocale);

  return (
    <>
      <div style={{ display: 'flex', flexDirection: 'column', gap: 'clamp(15px, 3vw, 30px)' }}>
        <Hero content={content.hero} />
        <Concept content={content.concept} />
        <Games content={content.games} />
        <Pricing content={content.pricing} />
        <Contact content={content.contact} />
      </div>
      <Footer content={content} />
      <WhatsAppButton />
    </>
  );
}
